<template>

  <div id="ccscClassroom">
    <my-header :title="'原原课堂'"></my-header>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import myHeader from '../../components/myHeader'

export default {
  name: 'ccscClassroom',
  components: {
    myHeader
  },
  data () {
    return {}
  },
  mounted () {
  },
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  #ccscClassroom {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  #ccscClassroom .content {
    position: fixed;
    bottom: 0;
    top: 3rem;
    width: 100%;
    left: 0;
    overflow-y: auto;
  }
</style>



